import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { ChevronLeft, ChevronRight, Phone, Mail, MapPin } from 'lucide-react';
import './Home.css';

const Home = () => {
  const navigate = useNavigate();
  const [isDarkMode, setIsDarkMode] = useState(false);
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  
  const carouselImages = [
    '/image1.jpg',
    '/image2.jpg',
    '/image3.jpg',
    '/image4.jpg'
  ];

  useEffect(() => {
    const storedDarkMode = sessionStorage.getItem('darkMode');
    if (storedDarkMode === 'true') {
      setIsDarkMode(true);
      document.body.classList.add('dark-mode');
    }
  }, []);

  const nextImage = () => {
    setCurrentImageIndex((prevIndex) =>
      (prevIndex + 1) % carouselImages.length
    );
  };

  const prevImage = () => {
    setCurrentImageIndex((prevIndex) =>
      prevIndex === 0 ? carouselImages.length - 1 : prevIndex - 1
    );
  };

  return (
    <div className={`home-container ${isDarkMode ? 'dark-mode' : ''}`}>
      <header className="hero">
        <div className="hero-content">
          <h1>Her Durum in Bir Masa Ayrtn</h1>
          <p>lk bulumalardan, doum gnlerine, yldnmlerinden, i yemeklerine kadar hayat lezzetlidir. Neden en iyi puan alm restoranlarda rezervasyon yaparak kutlamyorsunuz?</p>
          <div className="hero-actions">
            <button onClick={() => navigate('/Restaurants')}>Restoranlar Kefedin</button>
          </div>
        </div>
      </header>

      <div className="red-panel-up"> </div>

      <section className="why-opentable">
        <h2>Neden Rezerve'den Rezervasyon Yapmalsnz?</h2>
        <p>Rezerve a restoranlar ve mterileri buluturur, doru masay bulup rezervasyon yapmanza yardmc olur ve restoranlarn kiiye zel hizmet sunarak mterilerini memnun etmelerine destek olur.</p>
      </section>

      <div className="white-panel">
        <div className="carousel-container">
          <button
            className="carousel-button prev"
            onClick={prevImage}
          >
            <ChevronLeft size={32} />
          </button>
          <div className="carousel-image-wrapper">
            <img
              src={carouselImages[currentImageIndex]}
              alt={`Restoran resmi ${currentImageIndex + 1}`}
              className="carousel-image"
            />
          </div>
          <button
            className="carousel-button next"
            onClick={nextImage}
          >
            <ChevronRight size={32} />
          </button>
        </div>
      </div>

      <div className="red-panel">
        <div className="contact-container">
          <h2 className="contact-title">Bize Ulan</h2>
          <div className="contact-info">
            <div className="contact-item">
              <Phone className="contact-icon" />
              <div>
                <h3>Telefon</h3>
                <p>+90 (212) 555 0123</p>
              </div>
            </div>
            <div className="contact-item">
              <Mail className="contact-icon" />
              <div>
                <h3>E-posta</h3>
                <p>iletisim@rezerve.com</p>
              </div>
            </div>
            <div className="contact-item">
              <MapPin className="contact-icon" />
              <div>
                <h3>Adres</h3>
                <p>Levent Mah. Bykdere Cad. No:123<br />ili, stanbul</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Home;