import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const API_BASE_URL = process.env.REACT_APP_API_BASE_URL;

const ForgotPassword = () => {
  const [email, setEmail] = useState(''); 
  const [message, setMessage] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const isEmailValid = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!email) {
      setError('Ltfen e-posta adresinizi girin.');
      return;
    }

    if (!isEmailValid(email)) {
      setError('Geerli bir e-posta adresi girin.');
      return;
    }

    setError('');

    try {
      const response = await axios.post(`${API_BASE_URL}/api/forgot`, {
        email
      });

      if (response.status === 200) {
        setMessage('E-posta gnderildi!');
        navigate('/reset-password');
      }
    } catch (error) {
      console.error("Password reset request failed:", error);
      setError('ifre sfrlama talebi srasnda bir hata olutu. Ltfen tekrar deneyin.');
    }
  };

  const titleStyle = {
    fontSize: '32px',
    fontWeight: 'bold',
    color: 'black',
    textAlign: 'center',
    marginBottom: '20px',
    fontFamily: "'Be Vietnam Pro', 'sans-serif'",
    padding: '20px'
  };

  return (
    <div className="forgot-password">
      <h2 style = {titleStyle}>ifremi Unuttum</h2>
      <form onSubmit={handleSubmit}>
        {error && <p className="error-message">{error}</p>}
        {message && <p className="success-message">{message}</p>}

        <div className="input-group">
          <label htmlFor="email">E-posta adresiniz</label>
          <input
            type="email"
            id="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
        </div>

        <button type="submit">ifre sfrlama talebi</button>
      </form>
    </div>
  );
};

export default ForgotPassword;


