import React from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import UserSignupForm from '../components/UserSignupForm';
import './UserSignup.css';

const API_BASE_URL = process.env.REACT_APP_API_BASE_URL;

const UserSignup = () => {
  const navigate = useNavigate();

  const handleUserSignup = async (formData) => {
    try {
      const response = await axios.post(`${API_BASE_URL}/api/users`, formData);

      if (response.status === 200) {
        alert('Kayt baarl! Giri sayfasna ynlendiriliyorsunuz.');
        setTimeout(() => navigate('/login'), 1000);
      }
    } catch (error) {
      if (error.response && error.response.status === 500) {
        alert('Bu e-posta veya telefon numaras ile bir hesap zaten var. Ltfen giri yapn.');
      } else {
        console.error('Kayt ilemi baarsz:', error);
        alert('Kayt ilemi baarsz! Ltfen bilgilerinizi kontrol edin.');
      }
    }
  };

  const titleStyle = {
    fontSize: '32px',
    fontWeight: 'bold',
    color: 'black',
    textAlign: 'center',
    marginBottom: '20px',
    fontFamily: "'Be Vietnam Pro', 'sans-serif'",
    padding: '20px'
  };

  return (
    <div className="signup-page">
      <h2 style={titleStyle}>Kullanc Kayd</h2>
        <UserSignupForm onSubmit={handleUserSignup} />
    </div>
  );
};

export default UserSignup;



