import React, { useState } from 'react';

const ResetPasswordForm = ({ onSubmit }) => {
  const [token, setToken] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [error, setError] = useState('');

  const validateForm = () => {
    if (!token) {
      setError('Ltfen ifre sfrlama tokenn girin.');
      return false;
    }
    if (!newPassword || newPassword.length < 6 || newPassword.length > 12) {
      setError('ifre 6 ile 12 karakter arasnda olmaldr.');
      return false;
    }
    setError('');
    return true;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validateForm()) {
      onSubmit({ token, newPassword });
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      {error && <p className="error-message">{error}</p>}
      <div className="input-group">
        <label htmlFor="token">ifre Sfrlama Token</label>
        <input
          type="text"
          id="token"
          value={token}
          onChange={(e) => setToken(e.target.value)}
          placeholder="Tokennz girin"
        />
      </div>
      <div className="input-group">
        <label htmlFor="newPassword">Yeni ifre</label>
        <input
          type="password"
          id="newPassword"
          value={newPassword}
          onChange={(e) => setNewPassword(e.target.value)}
          placeholder="Yeni ifrenizi girin"
        />
      </div>
      <button type="submit">ifreyi Sfrla</button>
    </form>
  );
};

export default ResetPasswordForm;
