import React, { useState } from 'react';
import './LoginForm.css'

const ChangePasswordForm = ({ onSubmit }) => {
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');

  const isPasswordValid = (password) => {
    const hasUpperCase = /[A-Z]/.test(password);
    const hasLowerCase = /[a-z]/.test(password);
    const hasNumber = /\d/.test(password);
    const isCorrectLength = password.length >= 8 && password.length <= 12;
    return hasUpperCase && hasLowerCase && hasNumber && isCorrectLength;
  };

  const validateForm = () => {
    if (!currentPassword || !newPassword || !confirmPassword) {
      setError('Ltfen tm alanlar doldurun.');
      return false;
    }

    if (newPassword !== confirmPassword) {
      setError('Yeni ifre ve onay ifresi elemiyor.');
      return false;
    }

    if (!isPasswordValid(newPassword)) {
      setError('Yeni ifre 8 ile 12 karakter arasnda olmaldr. Bir byk harf, bir kk harf ve bir rakam iermelidir.');
      return false;
    }

    setError('');
    return true;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validateForm()) {
      onSubmit({ currentPassword, newPassword });
    }
  };

  return (
    <form onSubmit={handleSubmit} className="signup-form">
      {error && <p className="error-message">{error}</p>}

      <div className="input-group">
        <label htmlFor="currentPassword">Mevcut ifre</label>
        <input
          type="password"
          id="currentPassword"
          value={currentPassword}
          onChange={(e) => setCurrentPassword(e.target.value)}
        />
      </div>

      <div className="input-group">
        <label htmlFor="newPassword">Yeni ifre</label>
        <input
          type="password"
          id="newPassword"
          value={newPassword}
          onChange={(e) => setNewPassword(e.target.value)}
        />
      </div>

      <div className="input-group">
        <label htmlFor="confirmPassword">Yeni ifreyi Onayla</label>
        <input
          type="password"
          id="confirmPassword"
          value={confirmPassword}
          onChange={(e) => setConfirmPassword(e.target.value)}
        />
      </div>

      <button type="submit">ifreyi Deitir</button>
    </form>
  );
};

export default ChangePasswordForm;
