import React, { useState } from 'react';
import './LoginForm.css';

const availableTags = [
  'Pizza', 'Kebap', 'Vegan', 'Deniz rnleri', 'Hamburger', 'Steakhouse', 'Etnik', 'Casual Dining','Brasserie', 'Izgara', 
  'Kahve', 'Tatl', 'orba', 'Makarna', 'Sushi','Fine Dining', 'Fast Casual', 'Ak Bfe','Kahvalt', 'Brunch', 'Vejetaryen', 
  'Fast Food', 'arap', 'Bistro', 'Bar', 'Pub', 'Lounge', 'Kafe', 'Speakeasy','Gurme', 'Organik', 'Gluten Free', 'Balk', 
  'Dner', 'Pide', 'Ramen', 'Noodle','Akdeniz', 'Asya', 'Avrupa', 'Orta Dou', 'Gney Amerika', 'Kuzey Amerika', 'Uzak Dou',
];

const OwnerSignupForm = ({ onSubmit }) => {
  const [name, setName] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [address, setAddress] = useState('');
  const [twoPersonTables, setTwoPersonTables] = useState('');
  const [fourPersonTables, setFourPersonTables] = useState('');
  const [sixPersonTables, setSixPersonTables] = useState('');
  const [operatingHours, setOperatingHours] = useState('');
  const [photos, setPhotos] = useState([]);
  const [logo, setLogo] = useState(null);
  const [websiteLink, setWebsiteLink] = useState('');
  const [acceptConditions, setAcceptConditions] = useState(null);
  const [specialDays, setSpecialDays] = useState([]);
  const [additionalCondition, setAdditionalCondition] = useState('');
  const [tags, setTags] = useState([]); 
  const [error, setError] = useState('');

  const isPasswordStrong = (password) => {
    const hasUpperCase = /[A-Z]/.test(password);
    const hasLowerCase = /[a-z]/.test(password);
    const hasNumber = /\d/.test(password);
    const isCorrectLength = password.length >= 8 && password.length <= 12;

    return hasUpperCase && hasLowerCase && hasNumber && isCorrectLength;
  };

  const handleTagSelection = (tag) => {
    setTags((prev) => 
      prev.includes(tag) ? prev.filter((t) => t !== tag) : [...prev, tag]
    );
  };

  const handlePhotosChange = (e) => {
    const selectedFiles = Array.from(e.target.files);
    if (selectedFiles.length < 3) {
      setError('Ltfen en az 3 fotoraf ykleyin.');
      return;
    }
    setPhotos(selectedFiles);
    setError('');
  };

  const handleLogoChange = (e) => {
    setLogo(e.target.files[0]);
  };

  const handleSpecialDaysChange = (e) => {
    const value = e.target.value;
    setSpecialDays((prev) =>
      prev.includes(value) ? prev.filter((day) => day !== value) : [...prev, value]
    );
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (
      !name ||
      !phoneNumber ||
      !email ||
      !password ||
      !address ||
      photos.length < 3 ||
      !logo ||
      !twoPersonTables ||
      !fourPersonTables ||
      !sixPersonTables ||
      !operatingHours ||
      acceptConditions === null
    ) {
      setError('Ltfen tm zorunlu alanlar doldurun');
      return;
    }

    if (tags.length === 0) {
      setError('Ltfen en az bir etiket sein.');
      return;
    }

    if (name.length > 50) {
      alert('sim 50 karakterden uzun olamaz.');
      return;
    }

    const phoneRegex = /^[0-9]{5,10}$/;
    if (!phoneRegex.test(phoneNumber)) {
      setError('Geerli bir telefon numaras girin.');
      return;
    }

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(email)) {
      setError('Geerli bir e-posta adresi girin.');
      return;
    }

    if (!isPasswordStrong(password)) {
      setError(
        'ifre 8 ile 12 karakter arasnda olmaldr. Bir byk harf, bir kk harf ve bir say iermelidir.'
      );
      setPassword('');
      return;
    }

    const hoursRegex = /^\s*\d{1,2}\.\d{2}\s*-\s*\d{1,2}\.\d{2}\s*$/;
    if (!hoursRegex.test(operatingHours)) {
      setError('Geerli bir alma saatleri format girin (r. 09.00 - 22.00).');
      return;
    }
    const urlRegex = /^(https?:\/\/)?([\da-z.-]+)\.([a-z.]{2,6})([/\w .-]*)*\/?$/;
    if (websiteLink && !urlRegex.test(websiteLink)) {
      setError('Geerli bir website linki girin. rnek: https://www.ornek.com');
      return;
    }

    setError('');
    onSubmit({
      name,
      phoneNumber,
      email,
      password,
      address,
      photos,
      logo,
      twoPersonTables,
      fourPersonTables,
      sixPersonTables,
      operatingHours,
      websiteLink,
      acceptConditions,
      specialDays,
      additionalCondition: acceptConditions === 'yes' ? additionalCondition : '',
      tags,
    });
  };

  return (
    <form onSubmit={handleSubmit} className="signup-form">
      {error && <p className="error-message">{error}</p>}

      <div className="input-group">
        <label htmlFor="name">Restoran ad <span style={{ color: 'red' }}>*</span></label>
        <input
          type="text"
          id="name"
          value={name}
          onChange={(e) => setName(e.target.value)}
        />
      </div>

      <div className="input-group">
        <label htmlFor="address">Restoran adresi <span style={{ color: 'red' }}>*</span></label>
        <input
          type="text"
          id="address"
          value={address}
          onChange={(e) => setAddress(e.target.value)}
        />
      </div>

      <div className="input-group">
        <label htmlFor="phoneNumber">Telefon numaras <span style={{ color: 'red' }}>*</span></label>
        <input
          type="tel"
          id="phoneNumber"
          value={phoneNumber}
          onChange={(e) => setPhoneNumber(e.target.value)}
        />
      </div>

      <div className="input-group">
        <label htmlFor="email">E-posta <span style={{ color: 'red' }}>*</span></label>
        <input
          type="email"
          id="email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
      </div>

      <div className="input-group">
        <label htmlFor="password">ifre <span style={{ color: 'red' }}>*</span></label>
        <input
          type="password"
          id="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
      </div>

      <div className="input-group">
        <label htmlFor="twoPersonTables">2 kiilik masa says <span style={{ color: 'red' }}>*</span></label>
        <input
          type="number"
          id="twoPersonTables"
          value={twoPersonTables}
          min={0}
          onChange={(e) => setTwoPersonTables(e.target.value)}
        />
      </div>

      <div className="input-group">
        <label htmlFor="fourPersonTables">4 kiilik masa says <span style={{ color: 'red' }}>*</span></label>
        <input
          type="number"
          id="fourPersonTables"
          value={fourPersonTables}
          min={0}
          onChange={(e) => setFourPersonTables(e.target.value)}
        />
      </div>

      <div className="input-group">
        <label htmlFor="sixPersonTables">6 kiilik masa says <span style={{ color: 'red' }}>*</span></label>
        <input
          type="number"
          id="sixPersonTables"
          value={sixPersonTables}
          min={0}
          onChange={(e) => setSixPersonTables(e.target.value)}
        />
      </div>

      <div className="input-group">
        <label htmlFor="operatingHours">alma saatleri <span style={{ color: 'red' }}>*</span></label>
        <input
          type="text"
          id="operatingHours"
          value={operatingHours}
          onChange={(e) => setOperatingHours(e.target.value)}
          placeholder="rnek: 09.00 - 22.00"
        />
      </div>

        <div className="input-group">
          <label htmlFor="acceptConditions">
            artlarnz var m? <span style={{ color: 'red' }}>*</span>
          </label>
        <div style={{ display: 'flex', gap: '10px', alignItems: 'center' }}>
          <label style={{ display: 'flex', alignItems: 'center', gap: '5px' }}>
            <input
              type="radio"
              name="acceptConditions"
              value="yes"
              checked={acceptConditions === 'yes'}
              onChange={() => setAcceptConditions('yes')}
            />
            Evet
          </label>
          <label style={{ display: 'flex', alignItems: 'center', gap: '5px' }}>
            <input
              type="radio"
              name="acceptConditions"
              value="no"
              checked={acceptConditions === 'no'}
              onChange={() => setAcceptConditions('no')}
            />
            Hayr
          </label>
        </div>
        {acceptConditions === 'yes' && (
          <div>
            <label htmlFor="additionalCondition">artlarnz belirtin:</label>
            <input
              type="text"
              id="additionalCondition"
              value={additionalCondition}
              onChange={(e) => setAdditionalCondition(e.target.value)}
            />
          </div>
        )}
      </div>

      <div className="input-group">
        <label htmlFor="photos">Restoran fotoraflar (en az 3 adet) <span style={{ color: 'red' }}>*</span></label>
        <input
          type="file"
          id="photos"
          accept="image/*"
          multiple
          onChange={handlePhotosChange}
        />
      </div>

      <div className="input-group">
        <label htmlFor="logo">Restoran logosu <span style={{ color: 'red' }}>*</span></label>
        <input
          type="file"
          id="logo"
          accept="image/*"
          onChange={handleLogoChange}
        />
      </div>

      <div className="input-group">
        <label htmlFor="specialDays">
          zel gn rezervasyonlar alyor musunuz?
        </label>
        <div style={{ display: 'flex', flexDirection: 'column' }}>
          <label>
            <input
              type="checkbox"
              value="doum gn"
              checked={specialDays.includes('doum gn')}
              onChange={handleSpecialDaysChange}
            />
            Doum Gn
          </label>
          <label>
            <input
              type="checkbox"
              value="yldnm"
              checked={specialDays.includes('yldnm')}
              onChange={handleSpecialDaysChange}
            />
            Yldnm
          </label>
          <label>
            <input
              type="checkbox"
              value="i yemei"
              checked={specialDays.includes('i yemei')}
              onChange={handleSpecialDaysChange}
            />
             Yemei
          </label>
          <label>
            <input
              type="checkbox"
              value="evlilik teklifi"
              checked={specialDays.includes('evlilik teklifi')}
              onChange={handleSpecialDaysChange}
            />
            Evlilik Teklifi
          </label>
        </div>
      </div>

      <div className="input-group">
        <label>Restoran Etiketleri</label>
        <div className="tags-container">
          {availableTags.map((tag) => (
            <label key={tag} className="tag-option">
              <input
                type="checkbox"
                value={tag}
                checked={tags.includes(tag)}
                onChange={() => handleTagSelection(tag)}
              />
              {tag}
            </label>
          ))}
        </div>
      </div>

      <div className="input-group">
        <label htmlFor="websiteLink">Website linki</label>
        <input
          type="url"
          id="websiteLink"
          value={websiteLink}
          onChange={(e) => setWebsiteLink(e.target.value)}
        />
      </div>

      <button type="submit">Kayt Ol</button>
    </form>
  );
};

export default OwnerSignupForm;
